//Navigate: 렌더링될때 현재 위치를 변경함.
//useNavigate: 특정이벤트가 발생 했을때 url을 조작(이전,다음...)
import { Navigate } from "react-router-dom";

const Mypage = () => {
  const isLoggdIn = false; //기본은 로그인 되어있지 않은 상태 
  if(!isLoggdIn){
    return <Navigate to= "/login" replace={true} /> 
    //만약에 값이 false라면 login페이지로 이동.
  }
  return <div>마이페이지</div>
}

export default Mypage