import "./App.css";
import "bootstrap/dist/css/bootstrap.min.css";
import { Row, Col , Container} from "react-bootstrap";


import { useState, useEffect } from "react";
import mBanner from "./img/pc_main_02.jpg";

import { Route, Routes } from "react-router-dom";

import Company from "./routes/Company"; /* 파스쿠찌소개 */
import Product from "./routes/Product"; /* 파스쿠찌메뉴 */
import Info from "./routes/Info"; /* 매장안내 */
import Notice from "./routes/Notice"; /* 공지사항 */

import Footer from "./component/Footer";
import Header from "./component/Header";

import Login from "./pages/Login";
import Mypage from "./pages/Mypage";

import Event from "./routes/Event";
import Detail from "./routes/Detail";/* 상세페이지 */
import Cart from "./routes/Cart";

import SassComponent from "./SassComponent";
import CSSModule from "./CSSModule";
import StyledComponent from "./StyledComponent";

/* 상세페이지 이동 */
import { useNavigate } from "react-router-dom";

/* 동영상 플레이어 */
import ReactPlayer from "react-player";

import axios from "axios";
import data from "./data.js";

function App() {
  let obj = {name:'jeong'}
  // localStorage.setItem('data',obj)
  localStorage.setItem('data', JSON.stringify(obj) );

  //콘솔에서 확인
  let print = localStorage.getItem('data')
  // JSON 문자열을 객체로 변환 한 후 name속성의 값을 콘솔에 출력
  console.log(JSON.parse(print).name)

  let [goods, setGoods] = useState(data);
  let [click, setClick] = useState(0);

  const handleClick = () => {
    setClick((click += 1));
    console.log(click);
    axios
    .get(`https://inseo97.github.io/reactShop_ex/data${click+1}.json`)
    .then((res) => {
      console.log(res.data)
      let temp = [...goods, ...res.data]
      setGoods(temp)
    })
    .catch(()=>{
      console.log("실패")
    })
  }

  let navigate = useNavigate();
  function Card(props) {
    function imgClick() {
      navigate("/detail/" + props.i);
    }

    return (
      <Col sm>
        <img src={props.goods.img} alt="cake" width={200} onClick={imgClick} />
        <h4>{props.goods.title}</h4>
        <p>{props.goods.price}</p>
      </Col>
    );
  }

// 인트로
//state를 이용하여 초기 배너 보이도록 true로 설정
let [intro, setIntro] = useState(true) //true면 보이고 false면 안보이게
useEffect (() => {
  let time = setTimeout(()=>{
    setIntro(false)
  },3000)
})

  return (
    <div className="App">

      <Header />
      
      <Routes>
        <Route
          path="/"
          element={
            <>
              {/* intro page */}
              {/* <div>
                { // 참일때 인트로가 보여지도록
                  intro === true ? (
                    <div className="introWrap">
                      <h1>인트로페이지</h1>
                    </div>
                  ) :null
                }
              </div> */}

              {/* 비주얼 배너 영역 */}
              {/* 문자변수를 쓰고싶다면 변수 중간에 문자를 넣는다 */}
              <div
                className="main-gb"
                style={{
                  backgroundImage: "url(" + mBanner + ")",
                  height: "600px",
                  backgroundPosition: "center",
                }}
              ></div>

              {/* 리스트 영역1 */}
              <Container>
                <Row>
                  {/* map 반복문 만들기 */}
                  {goods.map((a, i) => {
                    return <Card goods={goods[i]} i={i + 1} key={i} />;
                  })}
                </Row>
                {
                  click < 2 ? (
                    <button
                    onClick={()=>{
                      axios
                      .get("https://inseo97.github.io./reactShop_ex/data2.json")
                      .then((result)=>{
                        let copy = [...goods]
                        copy.push(...result.data)
                        setGoods(copy)
                      })
                      //ajax 요청이 실패했을 때 메세지
                      .catch(()=>{
                        console.log("서버요청에 실패하였습니다.")
                      })
                      handleClick();
                    }}>
                    더보기
                  </button>
                  ) : <div>상품이 없습니다.</div>
                }
              </Container>
              {/* 리스트영역2 */}
              <Container>
                <Row>
                  <Col>
                  </Col>
                </Row>
              </Container>
              {/* 동영상영역 폴더는 pubilc폴더에 넣음. (컴파일과 빌드시 반드시 필요) */}
              <Container fluid className="contFirst videoWrap">
                <Row>
                  <video controls loop autoPlay muted>
                    <source src="/video/main.mp4"></source>
                  </video>
                </Row>
              </Container>

              {/* 유투브 */}
              <Container>
                <Col>
                  <ReactPlayer
                    className="react-player"
                    url={"https://youtu.be/FyG21rXCxlY?si=mxfYIdpmyWPfuUWv"}
                    width="100%"
                    height="500px"
                    playing={true}
                    muted={true}
                    controls={true}
                    light={false}
                    pip={true}
                    poster={"/video/vidbg.png"}
                  >

                  </ReactPlayer>
                </Col>
              </Container>

            </>
          }
        />
        <Route path="/Company" element={<Company />} />
        <Route path="/Product" element={<Product />} />
        <Route path="/Info"    element={<Info />} />
        <Route path="/Notice"  element={<Notice />} />

        <Route path="/Login"  element={<Login />} />
        <Route path="/Mypage"  element={<Mypage />} />
        <Route path="/Cart"  element={<Cart />} />
        <Route path="/Event"  element={<Event />}>
          <Route 
          path="before"  
          element={
            <>
              <div>지난이벤트</div>
            </>
          } 
          />
          <Route 
          path="ing"  
          element={
            <>
              <div>진행중인 이벤트</div>
            </>
          } 
          />
        </Route>

        <Route path="*"  element={<div>요청하신 페이지는 없는 페이지 입니다.</div>} />
        <Route path="/Detail/:id" element={<Detail goods={goods} />} />

        <Route path="/SassComponent"  element={<SassComponent />} />
        <Route path="/CSSModule" element={<CSSModule />} />
        <Route path="/StyledComponent" element={<StyledComponent />} />
      </Routes>

      <Footer />
    </div>
  );
}

export default App;
