import { useReducer } from "react"

function reducer(state,action){ // (저장될 값, 변경될 값)
  return {
    ...state,
    [action.name] : action.value //변경될 고유이름 name명의 변경 될 내용을 input으로 보내는 작업
  }
}

const Login = () => {
  const [state, dispatch] = useReducer(reducer,{ // []는 배열을 나타냄
    name:'',
    nickname:''
  })
  //비구조화 할당.
  const {name, nickname} = state; // {}배열의 객체들을 담을 때 사용

  // const onChangeName = (e) => {
  //   setName(e.target.value)
  // }
  // const onChangeNickname = (e) => {
  //   setNickname(e.target.value)
  // }

  const onChange = e => { // onChange 이벤트가 발생하면 dispatch를 통해 액션 발생
    dispatch(e.target)
  }
  return (
    <>
      <div>
        로그인 페이지
      </div>
      <div>
        <input name="name" value={name} onChange={onChange} />
        <input name="nickname" value={nickname} onChange={onChange} />
      </div>

      <div>
        <b>이름 : </b> {name}
        <b>닉네임 : </b> {nickname}
      </div>
    </>
  )
}

export default Login