import "bootstrap/dist/css/bootstrap.min.css";
import { Container, Row, Col, Button } from "react-bootstrap";
import { useParams, Navigate, useNavigate } from "react-router-dom";
import "../App.css";
import { useEffect, useState } from "react";
//addItem가져오기
import { addItem } from "../store/cartSlice";
import { useDispatch } from "react-redux";

const Detail = (props) => {
  let dispatch = useDispatch(); //데이터 보내주는 함수
  let Navigate = useNavigate(); //useNavigate()를 실행하면 페이지 이동을 할 수 있음.
  //인풋 태그에 입력값 변수로 만들기
  let [num, setNum] = useState(""); //인풋태그에 입력할 초기 인풋값 비워두기

  useEffect(() => {
    //1.사용자가 Detail페이지 처음으로 접속하면
    let takeOut = localStorage.getItem("watched"); //2.watched 데이터 가져오고

    //3.localStorage에 추가하기
    // takeOut이 null이면 빈 배열로 초기화, 아니면 JSON을 오브젝트형으로 변환
    takeOut = takeOut ? JSON.parse(takeOut) : []; //JSON을 오브젝트형으로 변환하여

    takeOut.push(choose.id); //선택한 상품을 추가하하고
    takeOut = new Set(takeOut); //중복제거
    takeOut = Array.from(takeOut); //중복제거한 것을 다시 array로 변환
    localStorage.setItem("watched", JSON.stringify(takeOut)); //추가된 상품을 watched에 저장
  });

  useEffect(() => {
    if (isNaN(num) === true) {
      //숫자외 입력하면 경고 띄우기
      alert("숫자만 입력하세요");
    }
  });

  useEffect(() => {
    console.log("안녕");
  });
  let [count, setCount] = useState(0);
  //App.js의 goods 데이터 전달 받음.
  let { id } = useParams();
  console.log(id);

  //.find()라는 문법을 사용하면 array자료안에서 원하는 항목만 찾을 수 있음.
  let choose = props.goods.find(function (a) {
    return a.id == id; //array자료.id == url에 입력한 번호와 같을 경우 변수 choose에 담아
  });

  return (
    <>
      <Container className="detailWrap">
        <Row>
          <Col sm={6}>
            {/*data.js의 이미지경로 */}
            <img src={choose.img} width="200" alt="listImg" />
          </Col>
          <Col sm={4}>
            <h4>{choose.title}</h4> {/* 상품명 */}
            <p>{choose.content}</p> {/* 상품설명 */}
            <p>{choose.price}</p> {/* 가격 */}
            <Button
              variant="danger"
              onClick={() => {
                dispatch(
                  addItem({
                    id: choose.id,
                    img: choose.img,
                    name: choose.title,
                    price: choose.price,
                    count: 1,
                  })
                );
                Navigate("/cart");
              }}
            >
              주문하기
            </Button>
          </Col>
        </Row>
      </Container>
    </>
  );
};

export default Detail;
